* {
  margin: 0;
  padding: 0;
}

.header__logo {
  width: 100%;
  height: 111px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: #ffffff;
}

.header__logo--image {
  width: 201px;
  height: 52px;
}

@media all and (min-width: 768px) {
  .header__logo {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-left: 40px;
  }
}
